"use client";
import { TimelineView } from '../components/Timeline/TimelineView';
import { useTimelineStore } from '../stores/timelineStore';

export default function Page() {
  const rows = useTimelineStore((s) => s.rows);
  const tasks = useTimelineStore((s) => s.tasks);
  const updateTask = useTimelineStore((s) => s.updateTask);

  return (
    <div>
      <h1 className="text-2xl font-semibold mb-4">Timeline / Gantt Demo</h1>
      <div className="bg-white p-4 rounded shadow">
        <TimelineView rows={rows} tasks={tasks} startDate={new Date(2024, 0, 1)} endDate={new Date(2024, 2, 31)} viewMode="week" onTaskUpdate={(id, u) => updateTask(id, u)} />
      </div>
    </div>
  );
}
